'use strict';

var assign = require('object-assign');
var React = require('react/addons');
var Reflux = require('reflux');
var FiltersViewActions = require('../actions/filtersViewActions');
var FiltersViewStore = require('../stores/filtersViewStore');


var filters = [
  {
    id: 'country',
    main: true,
    name: 'Country',
    glyphicon: 'globe'
  },
  {
    id: 'sector',
    main: true,
    name: 'Sector',
    glyphicon: 'grain'
  },
  {
    id: 'finance',
    main: true,
    name: 'Finance',
    glyphicon: 'piggy-bank'
  },
  {
    id: 'type-of-assistance',
    main: true,
    name: 'Type of Assistance',
    glyphicon: 'text-size'
  },
  {
    id: 'approval-year',
    main: true,
    name: 'Approval Year',
    glyphicon: 'calendar'
  },
  {
    id: 'advanced-filters',
    main: false,
    name: 'Advanced Filters',
    glyphicon: 'menu-hamburger'
  },
  {
    id: 'basemap',
    main: false,
    name: 'Basemap',
    glyphicon: 'globe',
  },
  {
    id: 'share',
    main: false,
    name: 'Share',
    glyphicon: 'link'
  }
];


var FilterNavButton = React.createClass({displayName: "FilterNavButton",
  toggleMe: function(e) {
    if (e) { e.preventDefault() };  // if this is coming from a click event
    if (this.props.active) {
      FiltersViewActions.deactivate(this.props.id);
    } else {
      FiltersViewActions.activate(this.props.id);
    }
  },

  render: function() {
    var buttonClasses = React.addons.classSet({
      'active': this.props.active,
      'nav-button': true,
      'nav-button-main': this.props.main
    });
    return (
      React.createElement("li", {role: "presentation", className: buttonClasses}, 
        React.createElement("a", {href: "#", onClick: this.toggleMe}, 
          React.createElement("span", {className: 'glyphicon glyphicon-' + this.props.glyphicon, "aria-hidden": "true"}), 
          this.props.name
        )
      )
    );
  }
});


var FilterBody = React.createClass({displayName: "FilterBody",
  render: function() {
    return React.createElement("h3", null, this.props.name)
  }
});


module.exports = React.createClass({displayName: "exports",

  mixins: [Reflux.connect(FiltersViewStore)],

  render: function() {

    var buttons = filters.map(function(filter) {
      var isActive = filter.id === this.state.currentlyActive;
      return React.createElement(FilterNavButton, React.__spread({key: filter.id},  filter, {active: isActive}));
    }, this);

    var activeFilter;
    if (this.state.currentlyActive) {
      filters.forEach(function(filter) {
        if (filter.id === this.state.currentlyActive) {
          activeFilter = (
            React.createElement("div", {className: "filters-body"}, 
              React.createElement(FilterBody, React.__spread({},  filter))
            )
          );
        }
      }, this);
    }

    return (
      React.createElement("div", {className: "filters"}, 

        React.createElement("div", {className: "filters-nav"}, 
          React.createElement("h3", {className: "filters-title"}, 
            React.createElement("span", {className: "glyphicon glyphicon-chevron-right", "aria-hidden": "true"}), 
            "Quick Filters"
          ), 

          React.createElement("ul", {className: "nav nav-stacked filters-nav-buttons"}, 
            buttons
          )
        ), 

        activeFilter

      )
    );
  }

});
